import { FC } from "react"
import styled from "styled-components";

export const Header: FC = ({ children }) => {
  return (
    <Wrapper>
      {children}
    </Wrapper>
  )
}

const Wrapper = styled.header`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 65px;
  padding: 0 30px;
  background: #f5f5f5;
  box-shadow: 0 2px 5px 0 rgb(0 0 0 / .1);
`